import { Form, Field } from "react-final-form";
import { Switch } from "@mui/material";
import { makeStyles } from "@mui/styles";
import "./styles.css";

const colors = {
  darkGrey: "#2F313B",
  grey: "#6C7080",
  cyan: "#25B4C8",
  middleGrey: "#525566",
  lightGrey: "#6C7080",
  gray: "#474A59",
  red: "crimson",
  hoverGray: "#3B3E4A"
};

const sizes = {
  m: {
    width: 40,
    height: 24,
    borderRadius: 12,
    thumbWidth: 16
  },
  l: {
    width: 52,
    height: 32,
    borderRadius: 16,
    thumbWidth: 24
  }
};

const useStyles = makeStyles({
  root: ({ size }) => ({
    width: `${sizes[size].width}px !important`,
    height: `${sizes[size].height}px !important`,
    padding: "0px !important",
    boxSizing: "border-box !important",
    "&:hover": {
      "& $track": {
        background: `${colors.hoverGray} !important`,
        border: `2px solid ${colors.lightGrey} !important`,
        boxSizing: "border-box !important"
      }
    }
  }),
  track: ({ size }) => ({
    width: "100px !important",
    background: `${colors.darkGrey} !important`,
    border: `2px solid ${colors.middleGrey} !important`,
    opacity: "1 !important",
    borderRadius: `${sizes[size].borderRadius}px !important`,
    boxSizing: "border-box !important"
  }),
  thumb: ({ size }) => ({
    width: `${sizes[size].thumbWidth}px !important`,
    height: `${sizes[size].thumbWidth}px !important`,
    boxShadow: "2px 2px 2px rgba(0, 0, 0, 0.25) !important"
  }),
  switchBase: {
    top: "4px !important",
    left: "4px !important",
    padding: "0px !important",
    color: `${colors.grey} !important`
  },
  checked: ({ size }) => ({
    left: `${size === "m" ? 0 : 4}px !important`,
    color: `${colors.cyan} !important`
  }),
  disabled: {
    "& $thumb": {
      background: `${colors.middleGrey} !important`,
      color: `crimson !important`
    },
    "& ~ $track": {
      border: `2px solid ${colors.middleGrey} !important`,
      background: `${colors.gray} !important`
    }
  }
});

const SwitchInput = ({ input, meta }) => {
  const classes = useStyles({ size: "m" });
  return <Switch classes={classes} />;
};

export default function App() {
  const onSubmit = (v) => {
    console.log("values: ", v);
  };
  return (
    <Form onSubmit={onSubmit}>
      {({ handleSubmit }) => (
        <form onSubmit={handleSubmit}>
          <Field name="switch" component={SwitchInput} />
        </form>
      )}
    </Form>
  );
}
